<template>
  <v-card
    elevation="24"
    class="mx-auto"
  >
    <v-carousel
      :continuous="true"
      :cycle="cycle"
      :show-arrows="true"
      hide-delimiter-background
      delimiter-icon="mdi-minus"
      show-arrows-on-hover
    >
      <v-carousel-item
        v-for="(image, i) in images"
        :key="i"
      >
        <div class="bg-image" :style="['background-image:url(']+image.url+[');']" >
        </div>
      </v-carousel-item>
    </v-carousel>
  </v-card>
</template>

<script>
  export default {
    name: "Carousel",
    props: {
        images: Array
    },
    data () {
      return {
        colors: [
          'green',
          'secondary',
          'yellow darken-4',
          'red lighten-2',
          'orange darken-1',
        ],
        cycle: false,
        slides: [
          'First',
          'Second',
          'Third',
          'Fourth',
          'Fifth',
        ],
      }
    },
  }
</script>